.home-page .video-conainer {
  width: 100%;
}

.home-page .video-conainer video {
  width: 100%;
}

.home-page .video-conainer .text-video {
  position: absolute;
  top: 85%;
  padding: 0 8%;
  margin: 0 auto;
  color: white;
}

.home-page .para {
  color: blue;
}

@media (max-width: 767px) {
  .home-page .para {
    color: green;
  }
}

@media (min-width: 768px) and (max-width: 1023px) {
  .home-page .para {
    color: red;
  }
}
